<template>
  <div class="Payment">
    <main>
      <div class="weui-cells weui-cells_form">
        <div class="weui-cell">
          <div class="weui-cell__hd">
            <label class="weui-label">联系人</label>
          </div>
          <div class="weui-cell__bd">
            <input class="weui-input" type="text" v-model="orderData.customerName" placeholder="请输入联系人">
          </div>
        </div>
        <div class="weui-cell">
          <div class="weui-cell__hd">
            <label class="weui-label">我的电话</label>
          </div>
          <div class="weui-cell__bd">
            <input class="weui-input" type="text" v-model="orderData.customerPhone" placeholder="请输入联系电话">
          </div>
        </div>
        <div class="weui-cell">
          <div class="weui-cell__hd">
            <label class="weui-label">下单时间</label>
          </div>
          <div class="weui-cell__bd">
            {{createday}}
            <span class="time">{{createTime}}</span>
          </div>
        </div>
        <div class="weui-cell">
          <div class="weui-cell__hd">
            <label class="weui-label">预计送达</label>
          </div>
          <div class="weui-cell__bd">
            {{serviceday}}
            <span class="time">{{serviceTime}}</span>
          </div>
        </div>
        <div class="weui-cell">
          <div class="weui-cell__hd">
            <label class="weui-label">房间号</label>
          </div>
          <div class="weui-cell__bd">
            <input class="weui-input" type="text" v-model="orderData.roomNum" placeholder="请输入房间号">
          </div>
        </div>
        <div class="weui-cell weui-cell_select weui-cell_select-after mode" @click="payTypeChange">
          <div class="weui-cell__hd" style="color:#b2b2b2">
            支付方式
          </div>
          <div class="weui-cell__bd">
            <p v-if="!orderData.payTypetext">请选择支付方式</p>
            {{orderData.payTypetext}}
          </div>
        </div>
        <div class="weui-cell">
          <div class="weui-cell__hd">
            <label class="weui-label">发票抬头</label>
          </div>
          <div class="weui-cell__bd">
            <input class="weui-input" type="text" v-model="orderData.invoiceHead" placeholder="请输入发票抬头">
          </div>
        </div>
        <div class="weui-cell">
          <div class="weui-cell__hd">
            <label class="weui-label">备注</label>
          </div>
          <div class="weui-cell__bd">
            <input class="weui-input" type="text" v-model="orderData.remark" placeholder="请输入备注">
          </div>
        </div>
        <div class="weui-cell">
          <div class="weui-cell__bd">
            订单详情
          </div>
        </div>
        <div class="weui-flex" v-for="(item,index) in orderFoodDetails" :key="index">
          <div>
            <div class="foodname">{{item.foodName}}</div>
          </div>
          <div class="weui-flex__item">
            <div class="placeholder">{{item.foodProvidesName}}提供
              <span>X{{item.foodNumber}}</span>
            </div>
          </div>
          <div>
            <div class="money">￥{{item.foodPrice*item.foodNumber |money}}</div>
          </div>
        </div>
      </div>
    </main>
    <div class="totalSum">
      <p>订单总额：
        <span class="Price">￥{{totalPrice |money}}</span>
      </p>
      <div class="btn">
        <a href="javascript:;" class="weui-btn weui-btn_primary" @click="payFn">立即支付</a>
        <a href="javascript:;" class="weui-btn weui-btn_primary" @click="cancelOrderfn">取消订单</a>
      </div>
    </div>
  </div>
</template>

<script type='text/ecmascript-6'>
import weui from "@/assets/js/weui.min";
import {
  emptytest,
  phonetest,
  money,
  DateFormat
} from "@/assets/js/filters.js";
import { foodBooking, common, Personal } from "@/assets/js/request.js";
export default {
  data() {
    return {
      payType: [],
      orderData: {
        customerName: null,
        customerPhone: null,
        hotelId: sessionStorage.getItem("hotelId"),
        invoiceHead: null,
        orderId: this.$route.params.orderId,
        payType: null,
        payTypetext: "",
        remark: null,
        roomNum: null
      },
      totalPrice: 0,
      orderFoodDetails: [],
      createTime: "",
      createday: "",
      serviceTime: "",
      serviceday: "",
      hotelId: sessionStorage.getItem("hotelId"),
      orderId: this.$route.params.orderId,
      faly: true
    };
  },
  mounted() {
    foodBooking.foodOrderDetails({
      Suffix:
        sessionStorage.getItem("hotelId") +
        "/order/" +
        this.$route.params.orderId,
      fn: data => {
        if (emptytest(data.customerName)) {
          this.orderData.customerName = data.customerName;
        }
        if (emptytest(data.customerPhone)) {
          this.orderData.customerPhone = data.customerPhone;
        }
        this.orderData.roomNum = data.roomNum;
        this.orderData.payType = data.payType;
        if (data.payType) {
          this.orderData.payTypetext = this.payType[data.payType].label;
        }
        this.orderData.invoiceHead = data.invoiceHead;
        this.orderData.remark = data.remark;
        this.orderFoodDetails = data.orderFoodDetails;
        this.orderFoodDetails.forEach(function(item) {
          this.totalPrice = this.totalPrice - 0;
          this.totalPrice += item.foodNumber * item.foodPrice;
        }, this);

        this.createday = DateFormat(
          new Date(data.createTime).getTime(),
          "yyyy-MM-dd"
        );
        this.createTime = DateFormat(
          new Date(data.createTime).getTime(),
          "hh:mm:ss"
        );
        this.serviceday = DateFormat(
          new Date(
            data.createTime - 0 + data.deliveryTime * 60 * 1000
          ).getTime(),
          "yyyy-MM-dd"
        );
        this.serviceTime = DateFormat(
          new Date(
            data.createTime - 0 + data.deliveryTime * 60 * 1000
          ).getTime(),
          "hh:mm:ss"
        );
      }
    });
    this.$store.dispatch("getHotelInfo").then(() => {
      //判断是否微信登陆
      function isWeiXin() {
        var ua = window.navigator.userAgent.toLowerCase();
        if (ua.match(/MicroMessenger/i) == "micromessenger") {
          return true;
        } else {
          return false;
        }
      }
      //在线支付
      if (this.$store.state.HotelInfo.foodPayMode == 1) {
        if (!isWeiXin()) {
          this.payType = [
            {
              label: "支付宝",
              value: 0
            }
          ];
        } else {
          this.payType = [
            {
              label: "支付宝",
              value: 0
            },
            {
              label: "微信",
              value: 1
            }
          ];
        }
      } else if (this.$store.state.HotelInfo.foodPayMode == 2) {
        this.payType = [
          {
            label: "到店支付",
            value: 2
          }
        ];
      } else if (this.$store.state.HotelInfo.foodPayMode == 3) {
        if (!isWeiXin()) {
          this.payType = [
            {
              label: "支付宝",
              value: 0
            },
            {
              label: "到店支付",
              value: 2
            }
          ];
        } else {
          this.payType = [
            {
              label: "支付宝",
              value: 0
            },
            {
              label: "微信",
              value: 1
            },
            {
              label: "到店支付",
              value: 2
            }
          ];
        }
      }
    });
    Personal.myInfo({
      Suffix: this.hotelId,
      fn: data => {
        if (data) {
          this.orderData.customerName = data.nickname;
          this.orderData.customerPhone = data.phone;
        }
      }
    });
  },
  methods: {
    payTypeChange() {
      var _this = this;
      weui.picker(_this.payType, {
        onChange: function(result) {},
        onConfirm: function(result) {
          _this.orderData.payTypetext = result[0].label;
          _this.orderData.payType = result[0].value;
        }
      });
    },
    payFn() {
      if (!emptytest(this.orderData.customerName)) {
        this.Toast({
          message: "请输入联系人",
          duration: 2000
        });
        return false;
      }
      if (!phonetest(this.orderData.customerPhone)) {
        this.Toast({
          message: "请输入联系电话",
          duration: 2000
        });
        return false;
      }
      if (!emptytest(this.orderData.roomNum)) {
        this.Toast({
          message: "请输入房间号",
          duration: 2000
        });
        return false;
      }
      if (!emptytest(this.orderData.payType)) {
        this.Toast({
          message: "请输入选择支付类型",
          duration: 2000
        });
        return false;
      }
      if (this.faly) {
        this.faly = false;
        foodBooking.foodpay({
          Suffix: this.hotelId + "/pay",
          params: this.orderData,
          fn: data => {
            var newdata = data;
            this.faly = true;
            if (newdata) {
              common.getconfig({
                fn: data => {
                  if (this.orderData.payType == 2) {
                    this.$router.push("/administrators/mymeal");
                  } else {
                    location.href =
                      data.hostUrl +
                      "/mobile/78CDF1/food/" +
                      sessionStorage.getItem("hotelId") +
                      "/payJump/" +
                      newdata;
                  }
                }
              });
            } else {
              this.Toast({
                message: "支付失败",
                duration: 2000
              });
            }
          }
        });
      }
    },
    cancelOrderfn() {
      common.cancelOrder({
        Suffix: this.hotelId + "/cancel/" + this.orderId,
        fn: data => {
          this.Toast({
            message: "取消成功",
            duration: 2000
          });
          var _this = this;
          setTimeout(function() {
            _this.$router.go(-1);
          }, 1000);
        }
      });
    }
  }
};
</script>

<style scoped lang='less' rel='stylesheet/less'>
.Payment {
  display: flex;
  flex-flow: column;
  min-height: 100vh;
}

main {
  flex: 1;
  background: #f4f4f4;
}
</style>



